import React from 'react';

import { Navbar, Col, Row } from 'reactstrap';
import bn from '../utils/bemnames';

import logo from '../assets/img/logo/amway-logo-black.svg';
import menu1 from '../assets/icon/menus/menu1.png';
import calendar from '../assets/icon/menus/calendar.png';
import home from '../assets/icon/menus/home.png';
import menu4 from '../assets/icon/menus/menu4.png';

const bem = bn.create('header');
//const helper = require('../utils/helper');

class Header extends React.Component {
  state = {
    user: {},
  };

  componentDidMount() {
    let user = JSON.parse(localStorage.getItem('userdetails')) || {
      aboNumber: '1234567',
      name: 'Testing Name',
      initial: 'DE',
      title: 'Business Owner',
    };
    this.setState({
      user,
    });
  }

  render() {
    //const { user, isMenuOpen } = this.state;
    return (
      <div className={bem.b()}>
        <div className="desktop">
          <img className="logo" src={logo} />
          <div className="amway-menus">
            <a className="menu" href="/">
              Home
            </a>
            <a className="menu" href="/">
              Events
            </a>
            <a className="menu" href="/favorites">
              Favorites
            </a>
            <div className="menu"></div>
          </div>
        </div>
        <Navbar light expand className={`amway-menus mobile`}>
          <Row className="menuList">
            <Col md={3} xl={3} xs={3} className="menuItem">
              <div className="menu">
                <img src={menu1} />
              </div>
            </Col>
            <Col md={3} xl={3} xs={3} className="menuItem">
              <div className="menu">
                <img src={calendar} />
              </div>
            </Col>
            <Col md={3} xl={3} xs={3} className="menuItem">
              <div className="menu">
                <img src={home} />
              </div>
            </Col>
            <Col md={3} xl={3} xs={3} className="menuItem">
              <div className="menu">
                <img src={menu4} />
              </div>
            </Col>
          </Row>
        </Navbar>
      </div>
    );
  }
}

export default Header;
